.main {

    background-color: #ffadfa;
    display: flex;
    justify-content: center;

}

.mainContainer {
    margin: 100px 0;

    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.mainContainer * {
    border: 1px solid;

}

.photo {
    margin: 25px 0;
    width: 350px;
    height: 450px;
    background-color: #edffd8;
}

.welkomeBlock {
    margin: 25px 0;
    display: flex;
    height: 200px;
    flex-direction: column;
    justify-content: space-around;
}

@media (max-width: 576px) {
    .mainContainer {
        margin: 50px 0;

    }

    .photo {
        margin: 25px 0;
        width: 250px;
        height: 350px;
        background-color: #edffd8;
    }

    .welkomeBlock {
        text-align: center;
        margin: 25px 0;
        width: 80%;
        display: flex;
        height: 250px;
        flex-direction: column;
        justify-content: space-around;
    }


}